@import url("https://fonts.googleapis.com/css2?family=Yeon+Sung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Patua+One&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: linear-gradient(to bottom right, #7655ff, #3d2c7e);
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

/* navigation bar styles */
nav {
  width: 20%;
  flex-direction: column;
  height: 100vh;
  background-color: #3c8f7c;
  position: absolute;
  right: 0%;
  width: 0;
  overflow: hidden;
  transition: 0.5s ease;
  z-index: 10;
}
.open {
  width: 20%;
}

.navList {
  width: 100%;
  list-style: none;
}
.navList li {
  color: #fff;
  text-decoration: none;
  color: #fff;
  font-weight: 500;
  font-size: 30px;
}
.navList li {
  cursor: pointer;
  margin: 15px 0;
}
.logo {
  font-family: "Yeon Sung", cursive;
  font-size: 40px;
}
.menu_btn {
  z-index: 12;
  position: absolute;
  width: 40px;
  height: 40px;
  right: 3%;
  top: 3%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
}
.menu_btn div {
  height: 6px;
  width: 40px;
  background-color: #fff;
}
/* home page styles */
.logo {
  width: 20%;
}
.homepage {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: .9fr 1fr;
}
.background {
  position: absolute;
  left: 0;
  z-index: -5;
}
.illustration {
  width: 80%;
  margin: 0 auto;
  margin-top: 40px;
  animation: animate_illustration 1s ease;
}
.phone {
  position: absolute;
  width: 6% !important;
  right: 8%;
  top: 18%;
}

.home_page_description {
  width: 100%;
  height: 100%;
  flex-direction: column;
  animation: animate_Div 1s ease-out;
}
.description_title {
  font-family: "Yeon Sung", cursive;
  font-size: 60px;
  margin-bottom: 10px;
  color: #fff;
}
.description_details {
  width: 60%;
  text-align: center;
  color: #fff;
  margin-bottom: 30px;
}
.description_details span {
  font-family: "Yeon Sung", cursive;
  font-size: 20px;
  color: #f47458;
}
.form_container {
  margin-top: 0;
  flex-direction: column;
  width: 100%;
}
.backimg {
  position: absolute;
  width: 100%;
  height: 100vh;
  z-index: -10;
}

.search {
  margin-bottom: 20px;
  width: 70%;
  height: 50px;
  border-radius: 30px;
  border: none;
  padding: 0 20px;
}
.search:focus {
  outline: none;
  border: 2px solid #353349;
}
.btn {
  height: 45px;
  width: 150px;
  background-color: #f47458;
  color: #fff;
  font-weight: 500;
  border-radius: 15px;
  cursor: pointer;
}
.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.center2 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.dark {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.623);
  position: absolute;
  display: none;
  z-index: 5;
}
.block {
  display: block;
}

/* contribution page */
.contribution {
  width: 100%;
 height: 100vh;
}
.contribution_logo {
  color: #fff;
  font-family: "Yeon Sung", cursive;
  font-size: 50px;
  padding-left: 20px;
  padding-top: 10px;
}
.contribution_title {
  color: #fff;
  font-size: 50px;
  width: 100%;
  text-align: center;
}
.contribution_form_container {
  width: 63%;
  margin: 0 auto;
  padding-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.input_container {
  padding: 10px;
  transform: translateY(110vh);
  animation: animate_input 0.7s ease-out forwards;
}

.input_container:nth-child(3) {
  animation-delay: 0.1s;
}
.input_container:nth-child(2) {
  animation-delay: 0.3s;
}
.input_container:nth-child(4) {
  animation-delay: 0.5s;
}
.input_container:nth-child(5) {
  animation-delay: 0.7s;
}
.input_container:nth-child(6) {
  animation-delay: 0.9s;
}

.input_container label {
  color: #ffffff;
  font-size: 24px;
  font-family: "Patua One", cursive;
}
.input_container input,
textarea {
  border: none;
  border-bottom: 2px solid #f47458;
  margin-top: 10px;
  width: 100%;
  height: 50px;
  font-size: 18px;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  padding-left: 20px;
}
.input_container input:focus {
  outline: none;
}
.input_container textarea {
  margin-top: 10px;
  width: 100%;
  height: 200px;
  padding-top: 15px;
}
.text {
  grid-column-start: 1;
  grid-column-end: 3;
}
.left {
  justify-self: flex-end;
}
.thanks1 {
  position: absolute;
  height: 400px;
  right: 2%;
  top: 25%;
  animation: animate_Div 0.9s ease-out;
}
.thanks2 {
  position: absolute;
  height: 400px;
  left: 2%;
  top: 25%;
  animation: animate_illustration 0.9s ease-out;
}
.svgs {
  position: relative;
  top: 0;
  left: 100px;
  min-height: 100vh;
}
.form_error {
  width: 50%;
  height: 49%;
  background-color: #fff;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  transition: 0.9s ease;
  padding:20px;
  top: 150%;
  display: none;
  padding-top: 10vh;
}
.thanks_msg{
  padding-top: 10px;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.thanks_container{
  width: 100%;
  height: 10vh;
  background-color: #2e8b76;
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  font-size: 28px;
  font-family: "Patua One", cursive;
  text-transform: capitalize;
  
}
.btn_container{
  width: 100%;
  justify-content: flex-end;
}
.thanks3{
height: 100%;
}
.thanks_msg2{
  color: #2b3d46;
  font-size: 23px;

}

/* animations */

@keyframes animate_illustration {
  0% {
    transform: translateX(-130%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes animate_Div {
  0% {
    transform: translateX(130%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes animate_input {
  0% {
    transform: translateY(110vh);
  }
  100% {
    transform: translateY(0%);
  }
}
@keyframes spining {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes spining2 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}

/* result page style*/
.result_page {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.result_title {
  color: #fff;
  padding: 20px 0;
  font-size: 45px;
}
.data {
  margin: 50px auto;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  width: 40%;
  padding-top: 10vh;
  position: relative;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.452);
  animation: animate_input 0.7s ease-out forwards;
  margin-top: 8%;
}
.data_element {
  width: 100%;
  padding: 15px;
}
.element_title {
  margin-top: 10px;
  font-size: 22px;
  color: #1f8d73;
  font-family: "Patua One", cursive;
  font-weight: 500;
}
.data_heading {
  width: 100%;
  height: 10vh;
  background-color: #f47458;
  position: absolute;
  top: 0;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  color: #fff;
  font-size: 30px;
  font-family: "Patua One", cursive;
  text-transform: capitalize;
}
.data_heading i{
  margin-right: 10px;
}
.search_girl{
  position: absolute;
  height: 18%;
  top: 2.5%;
  right: 37%;
  z-index: 4;
  animation: animate_input 0.7s ease-out forwards;

}
.search_boy{
  position: absolute;
  width: 60%;
  top: 18.2%;
  right:-15%;
  z-index: 4;
  
}
.wind{
  position: absolute;
  top: 1%;
height: 95%;
left: 11%;
animation: animate_input 0.7s ease-out forwards;
z-index: -5;
}
.globe{
  position: absolute;
height: 30%;
left: 22%;
top: 3%;
z-index: -1;
animation: animate_input 0.7s ease-out forwards;
}
.element_details{
  color: #263238;
  text-transform: capitalize;
  font-size: 22px;
}



@keyframes resultsUp {
  from {
    transform: translateY(110vh);
  }
  from {
    transform: translateY(0%);
  }
}

/* loading page style*/
.loading_img {
  width: 40px;
  animation: rotate 0.9s linear infinite;
}
.looking {
  width: 450px;
}
.loading_container {
  margin-top: 30px;
  position: relative;
  width: 50%;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loading_title {
  color: #fff;
  font-size: 40px;
  margin-right: 20px;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media(max-width: 800px){
.result_page{
  justify-content: center;
}
 
  .data{
    width: 75%;
  }
  .search_girl,.search_boy,.globe{
    display: none;
  }

  .wind{
    display: none;
  }

  .cbtn{
    width: 100px;
  }
  .thanks_msg2{
    font-size: 15px;
  }
  .form_error{
    width: 80%;
  }
  .thanks_container{
font-size: 18px;
  }
  .contribution_form_container{
    width: 90%;
  }
  .contribution{
overflow: scroll;  }
  .home_page_description{
    animation: none;
   
  }
  .input_container{
    animation: none;
    transform: translateY(0);
  }
  .homepage{
    grid-template-columns: 1fr;
  }
  .illustration{
    display: none;
  }
  .open{
    width: 35%;
  }
  .contribution_form_container{
   display: flex;
   flex-direction: column;
  }
  .thanks1,.thanks2{
    display: none;
  }
}
@media(max-width: 600px){
  .menu_btn{
    top: 5%;
  }
  .element_details{
    font-size: 17px;
  }
  .element_title{
    margin-top: 0;
    font-size: 18px;
  }
  .data_heading{
    font-size: 20px;
  }
  .data{
    margin-top: 70px;
    animation:none;
    width: 85%;
  }
  .thanks3{
    height: 70%;
  }
  .thanks_msg{
    grid-template-columns: .3fr 1fr;
  }

  .open{
    width: 50%;
  }
}